            tooltip_data_tqxss.push(tipText_tqxss);
            let tqxss = {
              value: items.salesFiguresSame,
              name: tipText_tqxss
            };
            drawPie_data_tqxss.push(tqxss);

            let tipText_nltqxss =
              items.bureau +
              " (" +
              items.nsalesFiguresSame +
              ") " +
              ((items.nsalesFiguresSame / nltqxss_all) * 100).toFixed(2) +
