          var drawPie_data_nltqxse = [];
          var tooltip_data_nltqxse = [];
          var nltqxse_all = 0;

          resultData.forEach(items => {
            //
            xse_all += items.salesVolume;
            tqxse_all += items.salesVolumeSame;
            nltqxse_all += items.nsalesVolumeSame;
          });

          resultData.forEach(items => {
            //
            let tipText_xse =
              items.bureau +
