      });
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar2_startDate,
        endDate: this.calendar2_endDate
      };
      this.serverHost
        .fetchPost("/getPieInfo", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          var resultData = res.data.data;

          //
          var drawPie_data_mle = [];
          var tooltip_data_mle = [];
          var mle_all = 0;
          var drawPie_data_tqmle = [];
          var tooltip_data_tqmle = [];
          var tqmle_all = 0;
          var drawPie_data_nltqmle = [];
          var tooltip_data_nltqmle = [];
          var nltqmle_all = 0;

          resultData.forEach(items => {
            //
            mle_all += items.grossMargin;
            tqmle_all += items.grossMarginSame;
            nltqmle_all += items.ngrossMarginSame;
          });

          resultData.forEach(items => {
            //
            let tipText_mle =
              items.bureau +
              " (" +
              items.grossMargin +
              ") " +
              ((items.grossMargin / mle_all) * 100).toFixed(2) +
              "%";
            tooltip_data_mle.push(tipText_mle);
            let mle = {
              value: items.grossMargin,
              name: tipText_mle
            };
            drawPie_data_mle.push(mle);

            let tipText_tqmle =
              items.bureau +
              " (" +
              items.grossMarginSame +
              ") " +
              ((items.grossMarginSame / tqmle_all) * 100).toFixed(2) +
              "%";
            tooltip_data_tqmle.push(tipText_tqmle);
            let tqmle = {
              value: items.grossMarginSame,
              name: tipText_tqmle
            };
            drawPie_data_tqmle.push(tqmle);

            let tipText_nltqmle =
              items.bureau +
              " (" +
              items.ngrossMarginSame +
              ") " +
              ((items.ngrossMarginSame / nltqmle_all) * 100).toFixed(2) +
              "%";
            tooltip_data_nltqmle.push(tipText_nltqmle);
            let nltqmle = {
              value: items.ngrossMarginSame,
              name: tipText_nltqmle
            };
            drawPie_data_nltqmle.push(nltqmle);
          });
          this.resultData = resultData;

          //
          this.drawPie_data_mle = drawPie_data_mle;
          this.tooltip_data_mle = tooltip_data_mle;
          this.mle_all = mle_all;
          this.drawPie_data_tqmle = drawPie_data_tqmle;
          this.tooltip_data_tqmle = tooltip_data_tqmle;
          this.tqmle_all = tqmle_all;
          this.drawPie_data_nltqmle = drawPie_data_nltqmle;
          this.tooltip_data_nltqmle = tooltip_data_nltqmle;
          this.nltqmle_all = nltqmle_all;
          if (this.insideTabActive_2 == 0) {
            this.drawLine_tab2();
            this.drawLine_tab2_2();
          } else if (this.insideTabActive_2 == 1) {
            this.drawLine_tab2();
            this.drawLine_tab2_3();
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
